<template>
    <div class="query-box">
      <!-- 搜索固定标题行 -->
      <div class="query-box-title" @click="show = ! show"  ref="queryBox">
        <span class="search-title" v-show="queryData.length===0">查询条件</span>
        <div class="search-items">
          <span class="queryTerm" v-for="(item, index) in queryData" v-bind:key="index">
            <strong>{{item.name}}</strong>
            <span>
              {{item.text}}
            </span>
          </span>
        </div>
        <div style="text-align: right">
          <Button type="primary" size="small" icon="ios-arrow-dropdown" style="margin-right: 10px">查询条件</Button>
          <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="openAdd">添加</Button>
        </div>
      </div>

      <!-- 搜索查询条件下拉框 -->
      <div class="query-down" v-show="show"  ref="queryDown">
        <div class="queryTerm">
          <TextTerm titName="图层名称" titKey="srldName" @updataQueryData ="updataQueryData" @delQueryTrem="delQueryTrem"></TextTerm>
        </div>
        <div style="text-align: right;margin-top:-30px;">
          <Button type="primary" size="small" icon="ios-search" style="margin-right: 10px" @click="search">搜索</Button>
          <Button type="primary" size="small" icon="md-close" @click="show = false">取消</Button>
        </div>
      </div>
      <Modal
        title="新建地图图层"
        v-model="modal">
        <Form ref="add" :model="AddData" :rules="ruleValidate" :label-width="90" style="padding:0 50px;">
          <FormItem prop="name" label="图层名称：">
            <Input v-model.trim="AddData.name" placeholder="请输入图层名称" size="small"/>
          </FormItem>
          <FormItem prop="url" label="访问地址：">
            <Input v-model.trim="AddData.url" placeholder="请输入访问地址" size="small"/>
          </FormItem>
        </Form>
        <div slot="footer">
          <Button type="primary" @click="addSubmit">确定</Button>
        </div>
      </Modal>
    </div>
</template>

<script>
import TextTerm from '@/components/TextTerm'
import {
  addDitu
} from '@/api/acquisitionTable'
export default {
  name: 'queryBox',
  data () {
    return {
      show: false,
      /* 单位名称 */
      UnitName: '',
      queryData: [],
      modal: false, // 添加的弹框
      AddData: {
        name: '',
        url: '',
        createBy: 12
      },
      /* 表单校验 */
      ruleValidate: {
        name: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        url: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ]
      }
    }
  },
  components: {
    TextTerm
  },
  mounted () {
    document.addEventListener('click', this.queryHide)
  },
  beforeDestroy () {
    document.removeEventListener('click', this.queryHide)
  },
  methods: {
    /* queryData中对象的添加和删除
    * name 添加字段文本
    * text 添加字段值得文本
    * key 字段名
    * value 字段值 */
    updataQueryData (obj) {
      if (obj.text !== '') {
        this.queryData = this.queryData.filter((e) => e.name !== obj.name)
        this.queryData.push(obj)
      } else {
        this.queryData = this.queryData.filter((e) => e.name !== obj.name)
      }
    },
    /* 删除已选中查询条件 */
    delQueryTrem (key) {
      /* 删除对应字段的查询数据 */
      this.queryData = this.queryData.filter((obj) => obj.key !== key)
    },
    /* 搜索 */
    search () {
      let obj = {}
      for (let arr of this.queryData) {
        obj[arr.key] = arr.value
      }
      this.$emit('searchSrldId', obj)
      this.show = false
    },
    /* 点击任意地方隐藏搜索栏 */
    queryHide (e) {
      if ((!this.$refs.queryBox.contains(e.target)) && (!this.$refs.queryDown.contains(e.target))) {
        this.show = false
      }
    },
    /* 添加底图图层 */
    addSubmit () {
      this.$refs.add.validate( valid => {
        if (valid) {
          addDitu({
            data: {
              name: this.AddData.name,
              url: this.AddData.url,
              createBy: this.AddData.createBy
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '创建图层成功' })
              this.modal = false
              this.$parent.getSurveyTableList()
            } else {
              this.$Notice.error({ title: '创建图层失败' })
            }
          })
        }
      })
    },
    /* 打开弹框 */
    openAdd () {
      this.modal = true
    }
  }
}
</script>

<style scoped lang="scss">
  .query-box{
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    .query-box-title{
      line-height: 50px;
      font-size: 14px;
      overflow: hidden;
      padding: 0 0 10px;
      display: flex;
      justify-content: space-between;
      .queryTerm{
        margin-left: 10px;
        strong{
          margin-right: 10px;
          font-weight: normal;
        }
        span{
          padding:2px 10px;
          background: #61A7F3;
          border-radius: 4px;
          color:#fff;
          margin-left: 5px;
          margin-right: 25px;
          position: relative;
        }
      }
      .search-title {
        &:before {
          content: '';
          display: inline-block;
          width: 18px;
          height: 18px;
          background: url("../../assets/redLine/sprite.png") 0 -63px no-repeat;
          background-size: auto;
          vertical-align: middle;
          margin-right: 8px;
        }
      }
      .search-items {
        flex-grow: 1;
        padding: 0 5px;
        text-align: left
      }
      .btn-add {
        background-color: #01c296;
        border-color: #01c296;
        &:hover, &:focus {
          background-color: #05ab85;
          border-color: #05ab85;
        }
      }
    }
    .query-down{
      position: absolute;
      top: 50px;
      left: 5px;
      right: 5px;
      background: white;
      box-shadow: 3px 3px 5px rgba(0,0,0,.2);
      border: 1px solid #f5f5f5;
      border-radius: 6px;
      z-index: 99999999;
      padding: 10px 20px;
      .queryTerm{
        padding:10px 5px;
        text-align: left;
        .textTerm {
          span {
            width: 60px;
          }
        }
      }
    }
  }
  .blue-block{
    display: inline-block;
    background: #61a7f3;
    color: white;
    padding: 0 10px;
    border-radius: 3px;
    margin-right: 15px;
  }
</style>
